import React from "react";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";

import axios from "axios";

import { API_URL } from "../constants";

class NewSongForm extends React.Component {
    state = {
        pk: 0,
        artist: "",
        songKey: "",
        tempo: 0
    };

    componentDidMount() {
        if (this.props.song) {
            const { pk, artist, songKey, tempo } = this.props.song;
            this.setState({ pk, artist, songKey, tempo });
        }
    }

    onChange = e => {
        this.setState({ [e.target.name]: e.target.value });
    };

    createSong = e => {
        e.preventDefault();
        axios.post(API_URL, this.state).then(() => {
            this.props.resetState();
            this.props.toggle();
        });
    };

    editSong = e => {
        e.preventDefault();
        axios.put(API_URL + this.state.pk, this.state).then(() => {
            this.props.resetState();
            this.props.toggle();
        });
    };

    defaultIfEmpty = value => {
        return value === "" ? "" : value;
    };

    render() {
        return (
            <Form onSubmit={this.props.student ? this.editStudent : this.createStudent}>
                <FormGroup>
                    <Label for="artist">Artist:</Label>
                    <Input
                        type="text"
                        name="artist"
                        onChange={this.onChange}
                        value={this.defaultIfEmpty(this.state.artist)}
                    />
                </FormGroup>
                <FormGroup>
                    <Label for="songKey">Song Key:</Label>
                    <Input
                        type="songKey"
                        name="songKey"
                        onChange={this.onChange}
                        value={this.defaultIfEmpty(this.state.songKey)}
                    />
                </FormGroup>
                <FormGroup>
                    <Label for="tempo">tempo:</Label>
                    <Input
                        type="integer"
                        name="tempo"
                        onChange={this.onChange}
                        value={this.defaultIfEmpty(this.state.tempo)}
                    />
                </FormGroup>
                <Button>Send</Button>
            </Form>
        );
    }
}

export default NewSongForm;